import { Component, OnInit } from '@angular/core';
// import { CONFIG } from '../app.config'
import { AngularFire, FirebaseListObservable } from 'angularfire2';

@Component({
  selector: 'my-how',
  template: require('./how.component.html'),
  styles: [require('./how.component.scss')],
  inputs: ['enquiryForm']
})
export class HowComponent implements OnInit {
  enquiryForm = {
    name: null,
    role: null,
    email: null,
    phone: null,
    type: null,
    budget: null
  };
  formResponse = false;
  requiredField = false;

  public enquiryList: FirebaseListObservable<any>;
  constructor(af: AngularFire) {
    this.resetform()
    this.enquiryList = af.database.list('/enquiryList');
  }
  ngOnInit () {
    console.log('howdy')
  }
  resetform () {
    this.enquiryForm = {
      name: null,
      role: null,
      email: null,
      phone: null,
      type: null,
      budget: null
    }
  }
  submitForm () {
    if (this.enquiryForm.name.length && this.enquiryForm.role.length && this.enquiryForm.email.length && this.enquiryForm.phone.length && this.enquiryForm.type.length && this.enquiryForm.budget.length) {
      this.enquiryList.push(this.enquiryForm).then((item)=>{
        if (item) {
          this.formResponse = true
          this.resetform()
        }
      })
    } else {
      this.requiredField = true;
    }
  }
}


